.confirmOrderPage {
  height: 100vh;
  background-color: white;
  display: grid;
  grid-template-columns: 6fr 3fr;
}
.confirmOrderPage > div:last-child {
  border-left: 1px solid rgba(0, 0, 0, 0.247);
}
.confirmShippingArea {
  padding: 5vmax;
  padding-bottom: 2%;
}
.confirmShippingArea > p {
  font: 400 1.8vmax "Roboto";
}
.confirmShippingAreaBox,
.confirmCartItemsContainer {
  margin: 2vmax;
}
.confirmShippingAreaBox > div {
  display: flex;
  margin: 1vmax 0;
}
.confirmShippingAreaBox > div > p {
  color: black;
  font: 400 1.2vmax "Roboto";
}
.confirmShippingAreaBox > div > span {
  margin: 0 1vmax;
  font: 100 1.2vmax "Roboto";
  color: #575757;
}
.confirmCartItems > p {
  font: 400 1.8vmax "Roboto";
}
.confirmCartItems {
  padding: 5vmax;
  padding-top: 2vmax;
}
.confirmCartItemsContainer {
  max-height: 20vmax;
  overflow-y: auto;
}
.confirmCartItemsContainer > div {
  font: 400 1vmax "Roboto";
  display: flex;
  justify-content: space-around;
  margin: 2vmax 0;
  align-items: center;
}
.confirmCartItemsContainer > div > img {
  width: 3vmax;
}

.confirmCartItemsContainer > div > a {
  width: 60%;
  text-decoration: none;
  color: #575757;
  margin: 0 2vmax;
}
.confirmCartItemsContainer > div > span {
  font: 100 1vmax "Roboto";
  color: #5e5e5e;
}
.orderSummary {
  padding: 6vmax;
}

.orderSummary > p {
  text-align: center;
  padding: 1vmax;
  width: 100%;
  font: 400 1.8vmax "Roboto";
  margin: auto;
  border-bottom: 1px solid rgba(0, 0, 0, 0.267);
  box-sizing: border-box;
}
.orderSummary > div > div {
  display: flex;
  justify-content: space-between;
  margin: 2vmax 0;
  font: 300 1vmax "Roboto";
}
.orderSummary > div > div {
  color: rgba(0, 0, 0, 0.692);
}
.orderSummaryTotal {
  display: flex;
  font: 300 1vmax "Roboto";
  padding: 2vmax 0;
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, 0.363);
}
.orderSummary > button {
  border: none;
  background-color: tomato;
  color: white;
  font: 400 1vmax "Roboto";
  width: 100%;
  padding: 1vmax;
  /* cursor: pointer; */
  transition: all 0.5s;
  margin: auto;
}
.orderSummary > button:hover {
  background-color: rgb(200, 66, 46);
}

@media screen and (max-width: 600px){
    .confirmOrderPage{
        grid-template-columns: 1fr;
        height: unset;
    }
    .confirmOrderPage>div>:last-child{
        border-left: 0;
        border-top: 1px solid rgba(0, 0, 0, 0.247);
    }
    .confirmShippingArea>p{
        font: 400 6vw "Roboto";
    }
    .confirmShippingAreaBox>div{
        display: flex;
        margin: 6vw 0;
    }
    .confirmShippingAreaBox>div>p{
        font: 400 4vw "Roboto";
    }
    .confirmShippingAreaBox>div>span{
        font: 100 4vw "Roboto";
    }
    .confirmCartItems>p{
        font: 400 6vw "Roboto";
    }
    .confirmCartItemsContainer{
        max-height: 50vw;
    }
    .confirmCartItemsContainer>div{
        font: 400 4vw "Roboto";
        margin: 4vw 0;
    }
    .confirmCartItemsContainer>div>img{
        width: 10vw;
    }
    .confirmCartItemsContainer>div>a{
        margin: 0;
        width: 30%;
    }
    .confirmCartItemsContainer>div>span{
        font: 100 4vw "Roboto";
    }
    .orderSummary{
        padding: 12vw;
    }
    .orderSummary>p{
        font: 400 6vw "Roboto";
        padding: 4vw;
    }
    .orderSummary>div>div{
        font: 300 4vw "Roboto";
    }
    .orderSummaryTotal{
        font: 300 4vw "Roboto";
        padding: 5vw 0;
    }
    .orderSummary>button{
      padding: 4vw;
      margin: auto;
      font: 400 4vw "Roboto";
    }
}