.newProductContainer {
    width: 100%;
    box-sizing: border-box;
    background-color: rgb(221, 221, 221);
    border-left: 1px solid rgba(0, 0, 0, 0.158);
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  .newProductContainer h1 {
    color: rgba(0, 0, 0, 0.733);
    font: 300 2rem "Roboto";
    text-align: center;
  }
  .updateProductForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;
    justify-content: space-evenly;
    background-color: white;
    box-shadow:0 0 10px rgba(0, 0, 0, 0.267);
    height: 75%;
    width: 45vh;
  }
  .updateProductForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  .updateProductForm > div > input,
  .updateProductForm > div > select,
  .updateProductForm > div > textarea {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 1vmax cursive;
    outline: none;
  }
  .updateProductForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.6);
  }
  
  #updateProductFormFile > input {
    display: flex;
    padding: 0%;
  }
  #updateProductFormFile > input::file-selector-button {
    /* cursor: pointer; */
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.9vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    background-color: white
  }
  #updateProductFormFile > input::file-selector-button:hover {
    background-color: rgb(235, 235, 235);
  }
  #updateProductFormImage {
    width: 100%;
    overflow: auto;
  }
  #updateProductFormImage > img {
    width: 3vmax;
    margin: 0 0.5vmax;
  }
  #updateProductBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 1vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    /* cursor: pointer; */
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.22);
  }
  #updateProductBtn:hover {
    background-color: rgb(200, 66, 46);
  }
  
  @media screen and (max-width: 600px) {
    .newProductContainer {
      background-color: white;
    }
    .updateProductForm {
      padding: 5vmax;
    }
    .updateProductForm > div > input,
    .updateProductForm > div > select,
    .updateProductForm > div > textarea {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
    .updateProductForm > div > svg {
      font-size: 2.8vmax;
    }
    #updateProductFormImage > img {
      width: 8vmax;
      border-radius: 100%;
    }
  
    #updateProductFormFile > input::file-selector-button {
      height: 7vh;
      font: 400 1.8vmax cursive;
    }
    #updateProductBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }
  